/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
  background: #5591D4;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
@font-face {
  font-family: 'MyFont';
  src: url('../fonts/OpenSans-Bold.ttf') format('ttf'), url('../fonts/OpenSans-Regular.ttf') format('ttf');
  font-weight: 600;
  font-style: normal;
}
body {
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
}
.center {
  transform: translateXY(-50%);
}
/* imports
================= */
.tabs {
  position: relative;
}
.tabs__label {
  background: #EBEBEB;
  border-radius: 10px 10px 0 0;
  text-align: center;
  font-weight: bold;
  font-size: 1rem;
  color: #1D6AB7;
  padding: 4px 14px;
  margin: 0 1px;
  transition: background .225s ease;
  cursor: pointer;
}
.tabs__label.active,
.tabs__label:hover {
  background-color: #FFFFFF;
}
.tabs__pane {
  background: #FFFFFF;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
  border-radius: 16px;
  display: flex;
  align-content: center;
  justify-content: flex-start;
  padding: 20px 14px;
}
.tabs__pane__details {
  margin-left: 14px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-around;
}
.tabs__pane__details__label {
  color: #1D6AB7;
  font-weight: bold;
}
.tabs__pane__details__value {
  color: #606060;
}
.list {
  display: block;
}
.list--inline {
  display: flex;
  justify-content: center;
  align-items: center;
}
.list--inline > li {
  display: inline-block;
}



